import { EvaluationFormComponent } from './evaluation-form/evaluation-form.component';
import { ContactComponent } from './contact/contact.component';
import { ImacServiceComponent } from './imac-service/imac-service.component';
import { IpadServiceComponent } from './ipad-service/ipad-service.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule, Router, Event, NavigationStart, NavigationEnd } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { AboutComponent } from './about/about.component';
import { MacbookServiceComponent } from './macbook-service/macbook-service.component';
import { PartnershipComponent } from './partnership/partnership.component';
import { ReviewComponent } from './review/review.component';


const routes: Routes = [
{path : '', component: HomeComponent},
{path : 'about', component: AboutComponent},
{path : 'service/macbook', component: MacbookServiceComponent},
{path: 'service/iphone', component: IpadServiceComponent},
{path: 'service/imac', component: ImacServiceComponent},
{path: 'partnership', component: PartnershipComponent},
{path: 'contact', component : ContactComponent},
{path: 'evaluation', component : EvaluationFormComponent},
{path: 'review', component: ReviewComponent},
{path: '**', redirectTo: ''}

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {

 }
